import { ISubscribable } from "../../interfaces/actions/ISubscribable";

export class RWCActionHelper {
  /**
   * The prefix used for automatic action binding on HTML elements.
   */
  static RWC_ACTION_PREFIX = 'rwc';

  /**
   * Stores the index of the last initialized component with the auto action binding.
   */
  static lastComponentId: number = 1;

  /**
   * Gets the last initialized component with auto action binding.
   */
  static getLastComponentId(): number {
    return RWCActionHelper.lastComponentId++;
  }
  
  /**
   * Gets the default key for the component using passed name and the next available component index.
   */
  static getDefaultComponentKey(name: string): string {
    return `${RWCActionHelper.RWC_ACTION_PREFIX}-${name}-${RWCActionHelper.getLastComponentId()}`;
  }

  /**
   * Find elements by attribute name and component's key and add event listeners on click.
   */
  static bindRWCActions(component: ISubscribable, host: HTMLElement, actions: Array<string>, rwcKey: string, eventName: string = 'click'): void {
    if (actions.length > 0) {
      const querySelector = actions.map(action => `[data-${ RWCActionHelper.RWC_ACTION_PREFIX }-${ action }='${ rwcKey }']`).join(', ');
      document.querySelectorAll(querySelector).forEach(element => {
        actions.forEach(action => {
          if (element.getAttribute(`data-${ RWCActionHelper.RWC_ACTION_PREFIX }-${ action }`)) {
            const callback = this.createCallback(component, host, action);
            component.eventManager.registerEventListener(element, eventName, callback);
          }
        });
      });
    }
  }

  /**
   * Creates a callback method for the passed action of the passed component.
   */
  static createCallback(component: ISubscribable, host: HTMLElement, action: string): (event: Event | CustomEvent) => void {
    return (event: Event | CustomEvent) => {
      if ((event as CustomEvent).detail?.origin === host) {
        return;
      }
      component[action].call(component, event);
    }
  }
}
